<template>
    <div class="category-wrap">
        <h1>カテゴリー設定</h1>
        <div class="button-container">
            <button class="add-button" @click="add">追加する</button>
        </div>
        <div class="grid">
            <table id="categoryLayout">
                <thead>
                    <tr>
                        <th>カテゴリー名</th>
                        <th>色</th>
                        <th>表示</th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody v-if="categorys.length > 0">
                    <tr v-for="(category, value) in categorys" :key="value" :id="value">
                        <td class="category-name">{{ category.name }}</td>
                        <td class="color-box-column"><div class="color-box" :style="'background-color:' + category.color + ';'"></div></td>
                        <td v-if="category.display"><button class="display-btn" @click="toggleDisplay(category)">表示</button></td>
                        <td v-else><button class="display-btn hide" @click="toggleDisplay(category)">非表示</button></td>
                        <td><i class="fa-lg fa-solid fa-pen-to-square edit-button" @click="edit(category)"></i></td>
                        <td><i class="fa-lg fa-solid fa-trash delete-button" @click="removeItem(category)"></i></td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    name: 'CategoryList',
    data() {
        return {
            categorys: []
        }
    },
    mounted() {
        this.getCategorys();
        this.$emit('setDisplay', 'category');
    },
    emits:[
        'setDisplay',
        'openPop',
        'setData'
    ],
    methods: {

        getCategorys() {
            Ajax('/api/master/categorys')
            .then(res => res.json())
            .then(dat => this.categorys = dat);
        },
        /**
         * 
         * PUT
         * @param {*} e 
         */
        toggleDisplay(cat) {
            Ajax(`/api/master/categorys/${cat.id}`, 'PUT');
            this.getCategorys();
        },

        /**
         * 
         * DELETE
         * @param {*} e 
         */
        removeItem(cat) {
            Ajax(`/api/master/categorys/${cat.id}`, 'DELETE', cat);
            this.getCategorys();
        },

        edit(item) {
            this.$emit('setData', item);
            this.$emit('openPop');
        },

        add() {
            this.$emit('setData', null);
            this.$emit('openPop');
        }
    }
}
</script>
